import * as React from "react"
import { Link } from "gatsby"

const PostCard = ({
  variant,
  image,
  category,
  title,
  excerpt,
  author,
  date,
  slug,
}) => {
  if (variant === "large") {
    return (
      <article className="group cursor-pointer bg-white rounded-lg overflow-hidden editorial-shadow hover:shadow-xl transition-shadow">
        <div className="grid grid-cols-1 sm:grid-cols-[180px_1fr] md:grid-cols-[240px_1fr] gap-0">
          <div className="relative aspect-[4/3] sm:aspect-auto overflow-hidden">
            <img
              src={image}
              alt={title}
              className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
            />
          </div>

          <div className="p-6">
            <div className="mb-2">
              <span
                className="text-[var(--color-editorial-accent)]"
                style={{ fontSize: "var(--text-xs)" }}
              >
                {category}
              </span>
            </div>
            <h3
              className="mb-3 text-[var(--color-editorial-black)] group-hover:text-[var(--color-editorial-accent)] transition-colors"
              style={{ fontWeight: 600 }}
            >
              {title}
            </h3>
            {excerpt && (
              <p
                className="mb-4 text-[var(--color-editorial-grey)]"
                style={{ fontSize: "var(--text-sm)", lineHeight: "1.6" }}
              >
                {excerpt}
              </p>
            )}
            {(author || date) && (
              <div
                className="flex items-center gap-3 text-[var(--color-editorial-grey)]"
                style={{ fontSize: "var(--text-xs)" }}
              >
                {author && <span>By {author}</span>}
                {author && date && <span>•</span>}
                {date && <span>{date}</span>}
              </div>
            )}
          </div>
        </div>
      </article>
    )
  }

  if (variant === "small") {
    return (
      <article className="group cursor-pointer flex gap-4 items-start hover:bg-[var(--color-editorial-light-bg)] p-3 -m-3 rounded-lg transition-colors">
        <div className="relative w-20 h-20 flex-shrink-0 overflow-hidden rounded">
          <img
            src={image}
            alt={title}
            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
          />
        </div>

        <div className="flex-1 min-w-0">
          <div className="mb-1">
            <span
              className="text-[var(--color-editorial-accent)]"
              style={{ fontSize: "var(--text-xs)" }}
            >
              {category}
            </span>
          </div>
          <h4
            className="mb-2 text-[var(--color-editorial-black)] group-hover:text-[var(--color-editorial-accent)] transition-colors line-clamp-2"
            style={{ fontSize: "var(--text-sm)", fontWeight: 600 }}
          >
            {title}
          </h4>
          {(author || date) && (
            <div
              className="flex items-center gap-2 text-[var(--color-editorial-grey)]"
              style={{ fontSize: "var(--text-xs)" }}
            >
              {author && <span>{author}</span>}
              {author && date && <span>•</span>}
              {date && <span>{date}</span>}
            </div>
          )}
        </div>
      </article>
    )
  }

  if (variant === "category") {
    return (
      <article className="group cursor-pointer flex gap-4 items-start hover:bg-[var(--color-editorial-light-bg)] p-3 -m-3 rounded-lg transition-colors">
        <div className="relative w-20 h-20 flex-shrink-0 overflow-hidden rounded">
          <img
            src={image}
            alt={title}
            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
          />
        </div>

        <div className="flex-1 min-w-0">
          <div className="mb-1">
            <span
              className="text-[var(--color-editorial-accent)]"
              style={{ fontSize: "var(--text-xs)" }}
            >
              {category}
            </span>
          </div>
          <h4
            className="mb-2 text-[var(--color-editorial-black)] group-hover:text-[var(--color-editorial-accent)] transition-colors line-clamp-2"
            style={{ fontSize: "var(--text-sm)", fontWeight: 600 }}
          >
            {title}
          </h4>
          {excerpt && (
            <p
              className="text-[var(--color-editorial-grey)] line-clamp-3"
              style={{ fontSize: "var(--text-sm)", lineHeight: "1.5" }}
            >
              {excerpt}
            </p>
          )}
        </div>
      </article>
    )
  }
  if (variant === "category1") {
    return (
      <article className="group cursor-pointer bg-white rounded-lg overflow-hidden editorial-shadow hover:shadow-xl transition-shadow">
        <div className="grid grid-cols-[110px_1fr] gap-4 items-start">
          <div className="relative w-20 sm:w-[110px] h-16 sm:h-[80px] overflow-hidden rounded-md">
            <img
              src={image}
              alt={title}
              className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
            />
          </div>

          <div className="py-2 pr-3">
            <h3
              className="mb-1 text-[var(--color-editorial-black)] group-hover:text-[var(--color-editorial-accent)] transition-colors"
              style={{ fontWeight: 600, fontSize: "var(--text-base)" }}
            >
              {title}
            </h3>
            {excerpt && (
              <p
                className="text-[var(--color-editorial-grey)] line-clamp-3"
                style={{ fontSize: "var(--text-sm)", lineHeight: "1.5" }}
              >
                {excerpt}
              </p>
            )}
          </div>
        </div>
      </article>
    )
  }
  return (
    <Link to={slug} className="no-underline">
      <article className="group cursor-pointer bg-white rounded-lg overflow-hidden editorial-shadow hover:shadow-xl transition-shadow">
        <div className="relative aspect-[4/3] overflow-hidden">
          <img
            src={image}
            alt={title}
            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
          />
        </div>

        <div className="p-5">
          <div className="mb-2">
            <span
              className="text-[var(--color-editorial-accent)]"
              style={{ fontSize: "var(--text-xs)" }}
            >
              {category}
            </span>
          </div>
          <h3
            className="mb-3 text-[var(--color-editorial-black)] group-hover:text-[var(--color-editorial-accent)] transition-colors"
            style={{ fontWeight: 600 }}
          >
            {title}
          </h3>
          {excerpt && (
            <p
              className="mb-4 text-[var(--color-editorial-grey)]"
              style={{ fontSize: "var(--text-sm)", lineHeight: "1.6" }}
            >
              {excerpt}
            </p>
          )}
          {(author || date) && (
            <div
              className="flex items-center gap-3 text-[var(--color-editorial-grey)]"
              style={{ fontSize: "var(--text-xs)" }}
            >
              {author && <span>By {author}</span>}
              {author && date && <span>•</span>}
              {date && <span>{date}</span>}
            </div>
          )}
        </div>
      </article>
    </Link>
  )
}

export default PostCard
